:root {
  --light-gray: #e9e9e9;
  --gray: #7b7b7b;
  --dark-gray: #333333;
  --blue: #2196f3;
  --easy: #148d33;
  --medium: #e76712;
  --expert: #ce2914;
}

body {
  font-family: sans-serif;
  margin-bottom: 30px;
  padding: 0;
}

#root {
  width: 100%;
}

.centered-div {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.title {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 3rem;
  text-decoration: underline;
  margin-bottom: 70px;
  color: var(--dark-gray);
}

.square {
  background: var(--light-gray);
  border: 2px solid var(--gray);
  width: 50px;
  height: 50px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
}

.square.exploded {
  background: red;
}

svg {
  max-width: 48px;
  max-height: 48px;
}

.status {
  font-size: 1.7rem;
  margin-bottom: 20px;
  text-align: center;
  padding: 10px 0px;
  width: 100%;
}

.status.loss {
  background-color: #ff4949;
}

.status.win {
  background-color: #40fd88;
}

.board {
  display: grid;
}

.game-controls {
  width: 100%;
  margin-top: 50px;
}

.game-controls button {
  background-color: var(--blue);
  width: 150px;
  border: 2px solid var(--blue);
  color: white;
  font-size: 16px;
  cursor: pointer;
  padding: 12px;
  border-radius: 5px;
  transition-duration: 0.5s;
}

.game-controls button:hover {
  background-color: transparent;
  color: var(--blue);
}

.replay-button {
  float: right;
}

.difficulty-controls {
  margin-top: 50px;
}

.difficulty-controls span {
  display: block;
  margin-top: 5px;
}

.difficulty-controls button {
  width: 133px;
  color: white;
  font-size: 16px;
  cursor: pointer;
  padding: 12px;
  border-radius: 5px;
  border: 2px solid transparent;
  margin-top: 10px;
  transition-duration: 0.5s;
}

.difficulty-controls button:hover {
  background-color: transparent;
}

.easy-mode {
  background-color: var(--easy);
  margin-right: 20px;
}

.easy-mode:hover {
  border: 2px solid var(--easy);
  color: var(--easy);
}

.medium-mode {
  background-color: var(--medium);
  margin-right: 20px;
}

.medium-mode:hover {
  border: 2px solid var(--medium);
  color: var(--medium);
}

.expert-mode {
  background-color: var(--expert);
}

.expert-mode:hover {
  border: 2px solid var(--expert);
  color: var(--expert);
}
